import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Home from '@/pages/Home'
import Contacts from '@/pages/Contacts'
import PageNotFound from '@/pages/404'
import Catalog from '@/pages/Catalog'
import Product from '@/pages/Product'
import Profile from '@/pages/profile/Profile'
import HistoryBuy from '@/pages/profile/HistoryBuy'
import Review from '@/pages/Review'
import Information from '@/pages/Information'
import FAQ from '@/pages/faq'
import ContactUs from '@/pages/ContactUs'
import Manufactures from '@/pages/Manufactures'
import PaymentDelivery from '@/pages/PaymentDelivery'
import About from '@/pages/About'

import Cart from '@/pages/cart/Cart'
import Checkout from '@/pages/cart/Checkout'
import SuccessCheckout from '@/pages/cart/Success'

import DoAuth from '@/components/auth/DoAuth'

//   
const routes = [
    { path: '/', component: Home },
    { path: '/doAuth', component: DoAuth },
    { path: '/contacts', component: Contacts },
    { path: '/catalog', name: 'catalog', component: Catalog, meta: {breadcrumb: ' '}},
    { path: '/catalog/types=:types', component: Catalog, meta: {breadcrumb: ' '}},
    { path: '/catalog/search=:searchDetails', name: 'catalogSearch', component: Catalog, meta: {breadcrumb: ' '}},
    { path: '/product/:id', component: Product, meta: {breadcrumb: ''} },
    { path: '/profile', component: Profile, meta: {breadcrumb: ' '}},
    { path: '/profile/historybuy/:id', name: 'HistoryBuy', component: HistoryBuy, meta: {breadcrumb: ' ' }},
    /*  children: [
        { path: 'historybuy', name: 'HistoryBuy', component: HistoryBuy, meta: {breadcrumb: ' ' }}
      ] */ 
    { path: '/cart', component: Cart, meta: {breadcrumb: ''} },
    { path: '/checkout', component: Checkout, meta: {breadcrumb: ' '} },
    { path: '/cart/success', component: SuccessCheckout, meta: {breadcrumb: ' '}, props: { name: 'world' }},
    { path: '/review', component: Review, meta: {breadcrumb: '', title: ''} },
    { path: '/faq', component: FAQ, name: 'FAQ', meta: {breadcrumb: '  ', title: '  '} },
    { path: '/info', component: Information, name: 'Information', meta: {breadcrumb: '', title: ''} },
    { path: '/contact-us', component: ContactUs, name: 'ContactUs', meta: {breadcrumb: ' ', title: ' '} },
    { path: '/manufactures', component: Manufactures, name: 'Manufactures', meta: {breadcrumb: '', title: ''} },
    { path: '/payment-delivery', component: PaymentDelivery, name: 'PaymentDelivery', meta: {breadcrumb: '  ', title: '  '} },
    { path: '/about', component: About, name: 'About', meta: {breadcrumb: ' ', title: ' '} },
    { path: "*", component: PageNotFound }
  ]


//   
export default new Router({ 
  //mode: 'history',
  base: __dirname,
  routes, 
  linkActiveClass: 'current' 
})


